/* classes to support __HS.Connect.seeMore functionality.*/
.hs-more-content {
    display: none;
}

.hs-more-toggle:before {
    font-family: 'WebComponentsIcons';
    content: '\e014';
}

.hs-more-toggle.hs-more-open:before {
    content: '\e015';
}

// any div in a handshake component that has an onclick propery, make the cursor a pointer...
div[__hsrootnode] div[onclick] {
    cursor: pointer;
}
