// Some General Helper Classes

.hs-spedit-withperm {
    // elements with this class will be hidden until they are shown using code that 
    /// determines the user has permissions to edit that item. 
    display: none;
}

.hs-links-inactivelink {
    // used primary in a listview paging utility where we have a kendo generated <a> that 
    // should not be clickable or appear clickable.  may have other use cases in the future.
    pointer-events: none;
    cursor: default;
 }