// some customizations for k-cards 

.k-card {
    .k-card-header {

        @each $color,
        $value in $theme-colors {
            &.hs-card-header-#{$color} {
                background-color: $value !important;
                color: set-textcolor($value) !important;
            }
        }

        &.hs-card-header-border {
            border-bottom: 2px solid $accent-contrast;
        }

        .k-card-title {
            line-height: inherit;
        }
    }
}

.hs-card-action-header {
    display: grid;
    gap: 1em;
    grid-template-columns: 1fr min-content;
}

.hs-card-image-left img {
    width: 100%;
    padding: 0;
    float: clear;

    @media screen and (min-width: map-get($container-max-widths, "lg")) {
        float: left !important;
        padding-right: 1em;
        width: 50%;
    }
}

.hs-image-text-overlay {
    position: relative;

    img {
        width: 100%;
    }

    >div {
        position: absolute;
        top: 0;
        background-color: $accent-contrast;
        color: $white;
        opacity: .75;
        padding: 10px 10px;
        max-height: 92px;
        overflow: hidden;
    }
}

// Sean Scharlau's Card Ribbon Candy
.k-card {
    .hs-card-ribbon {
        width: 150px;
        height: 150px;
        overflow: hidden;
        position: absolute;
        z-index: 5;

        &:before,
        &:after {
            position: absolute;
            z-index: -1;
            content: '';
            display: block;
            border: 5px solid #2980b9;
        }

        >span {
            position: absolute;
            display: block;
            width: 225px;
            padding: 15px 0;
            background-color: #3498db;
            box-shadow: 0 5px 10px rgba(0, 0, 0, .1);
            color: #fff;
            font: 700 18px/1;
            text-shadow: 0 1px 1px rgba(0, 0, 0, .2);
            text-transform: uppercase;
            text-align: center;
        }

        &.top-right {
            top: -10px;
            right: -10px;

            &:before, &:after {
                border-top-color: transparent;
                border-right-color: transparent;
            }

            &:before {
                top: 0;
                left: 0;
            }

            &:after {
                bottom: 0;
                right: 0;
            }

            >span {
                left: -25px;
                top: 30px;
                transform: rotate(45deg);
            }
        }
    }
}