// Cosmetic fixes for Kendo/Bootstrap combinations 
// remove border for a k-widget using list-group 
.k-widget.list-group {
    border: none;
}

.hs-listgroup-grid {
    @extend .hs-grid;
    .list-group-item {
        border:1px !important;
    }
}

// hs-listview-border-0 must be applied to the listviews container element, not the list view. 
.hs-listview-border {
    .k-widget.k-listview.k-listview-bordered {
        border-width: $listview-border-width;
        border-color: $listview-border;
        border-style:solid;
    }
}

// turning off the default border around k-listview elements.  in the construction of actual components, almost 90% of the skins 
// required the border to be turned off to not look bad.  if there is a use case for the k-listview element to be bordered, 
// apply hs-listview-border to the Parent element of the HTML5ListView Component
.k-widget.k-listview.k-listview-bordered {
    border: none;
}


// if a list-group-item is in a k-card but not contained in a k-card-body element
// then remove the extra borders that appear on the right / left of each item 

.k-card {
    
    .k-listview-content.list-group>.list-group-item {
        border-right-width: 0;
        border-left-width: 0;

        &:first-child {
            border-top-width: 0;
        }

        &:last-child {
            border-bottom-width: 0;
        }
    }

    .k-card-body {
        .k-listview-content.list-group>.list-group-item {
            border-right-width: 1px;
            border-left-width: 1px;
    
            &:first-child {
                border-top-width: 1px;
            }
    
            &:last-child {
                border-bottom-width: 1px;
            }
        }
    }

}

// include a margin bottom for child elements of d-flex and flex-wrap
.d-flex.flex-wrap {
    >div {
        margin-bottom: .75rem;
    }
}

// csscode to support a centered view of the kendo page control

.hs-hide-outer-nav .k-pager-wrap>a.k-link.k-pager-nav.k-pager-first,
.hs-hide-outer-nav .k-pager-wrap>a.k-link.k-pager-nav.k-pager-last,
.hs-hide-inner-nav .k-pager-wrap>a.k-link.k-pager-nav:nth-child(2),
.hs-hide-inner-nav .k-pager-wrap>a.k-link.k-pager-nav:nth-last-child(2) {
    display: none;
}

.hs-center-listview-pager .k-pager-wrap .k-icon.k-i-seek-e,
.hs-center-listview-pager .k-pager-wrap .k-icon.k-i-seek-w,
.hs-center-listview-pager .k-pager-wrap .k-icon.k-i-arrow-e,
.hs-center-listview-pager .k-pager-wrap .k-icon.k-i-arrow-w {
    color: $link-color;
}

.hs-center-listview-pager .k-pager-wrap {
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding-bottom: 6px;
    padding-top: 6px;
}

.hs-center-listview-pager .k-pager-wrap>ul {
    padding-left: 10px;
    padding-right: 10px;
}

.hs-center-listview-pager .k-pager-wrap a.k-link,
.hs-center-listview-pager .k-pager-wrap span.k-state-selected {
    border: none;
}

.hs-center-listview-pager .k-pager-wrap .k-state-selected,
.hs-center-listview-pager .k-pager-wrap .k-link {
    min-width: 2.429em;
    line-height: 1.5;
}

.hs-center-listview-pager .k-pager-wrap a.k-link:focus {
    outline: none;
}

.k-pager-wrap ul.k-pager-numbers li.hs-listview-bullet-pager span>* {
    font-size: 1.5rem;
    color: $secondary;
}

.k-pager-wrap ul.k-pager-numbers li.hs-listview-bullet-pager span.k-state-selected {
    min-width: 0;

    >* {
        color: $primary;
    }
}


.hs-pager-minimal .k-pager-wrap.k-widget.k-floatwrap {
    background-color: transparent;
    padding: 0;
    border: none;

    .k-link,
    .k-state-selected {
        line-height: normal;
    }
}

div.hs-pager-dark .k-pager-wrap.k-widget.k-floatwrap {
    background-color: $dark;

    ul.k-pager-numbers li.hs-listview-bullet-pager span>* {
        color: $black;
    }

    ul.k-pager-numbers li.hs-listview-bullet-pager span.k-state-selected>* {
        color: $light;
    }
}

// when using a centered page control the default kendo css has some unwanted
// behavior when kendo adds k-pager-sm to the markup, this will override that.


.hs-center-listview-pager .k-pager-wrap.k-widget.k-floatwrap.k-pager-sm {

    select.k-dropdown,
    select.k-dropdowntree {
        display: none;
    }

    .k-pager-numbers {
        position: relative !important;
        left: 0px !important;
        display: block !important;
    }
}