:root {
    --component-color : #{$component-color};
    --component-bg : #{$component-bg};

    @each $accent,
    $value in $hs-accent-colors {
        --#{$accent}: #{$value};
    }

    @each $type,
    $value in $hs-theme-grays {
        --gray-#{$type}: #{$value};
    }

    --hs-grid-gap : #{$grid-gap};

    --hsteam-default-bg1 : #{$hsteam-default-bg1};
    --hsteam-default-bg2 : #{$hsteam-default-bg2};
    --hsteam-default-bg3 : #{$hsteam-default-bg3};
    --hsteam-default-bg-hover : #{$hsteam-default-bg-hover};
    --hsteam-default-card-bg : #{$hsteam-default-card-bg};
    --hsteam-default-card-title-bg : #{$hsteam-default-card-title-bg};
    --hsteam-default-border : #{$hsteam-default-border};
    --hsteam-default-text1 : #{$hsteam-default-text1};
    --hsteam-default-text2 : #{$hsteam-default-text2};

    --hsteam-dark-bg1 : #{$hsteam-dark-bg1};
    --hsteam-dark-bg2 : #{$hsteam-dark-bg2};
    --hsteam-dark-bg3 : #{$hsteam-dark-bg3};
    --hsteam-dark-bg-hover : #{$hsteam-dark-bg-hover};
    --hsteam-dark-card-bg : #{$hsteam-dark-card-bg};

    --hsteam-dark-border : #{$hsteam-dark-border};
    --hsteam-dark-text1 : #{$hsteam-dark-text1};
    --hsteam-dark-text2 : #{$hsteam-dark-text2};


}