.hs-upc .hs-upc-shade-secondary .title,
.hs-upc .hs-upc-shade-secondary .subtitle {
  color: #edf2f2;
}

.hs-upc .hs-upc-shade-secondary a {
  color: #edf2f2;
}

.hs-upc .hs-upc-h-group {
  margin: 0.75rem 0.75rem;
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 0.25rem;
}

.hs-upc .hs-upc-v-group {
  display: flex;
  margin-left: 0.75rem;
  margin-right: 0.75rem;
  gap: 0.25rem;
}

.hs-upc .hs-upc-v-group > .k-button {
  justify-content: flex-start;
}

.hs-upc .hs-upc-header .hs-upc-title {
  color: #5c6466;
  font-weight: 500;
  padding: 0.75rem 1.25rem;
}

.hs-upc .hs-upc-header .hs-upc-subtitle {
  color: #959a9c;
  padding: 0 0.75rem 0.75rem 1.25rem;
}

.hs-upc.hs-upc-nophoto {
  grid-template-columns: 1fr !important;
  grid-template-rows: repeat(3, min-content) !important;
  grid-template-areas: "header" "details" "footer" !important;
}

.hs-upc.hs-upc-nophoto .hs-upc-photo {
  display: none !important;
}

.hs-upc.hs-upc-stacked {
  grid-template-columns: 1fr;
  grid-template-areas: "photo" "header" "details" "footer";
  max-width: 24em;
}

.hs-upc.hs-upc-compact {
  grid-template-columns: minmax(25%, 150px) 1fr;
  grid-template-areas: "photo header" "details details" "footer footer";
}

.hs-upc.hs-upc-compact .hs-upc-photo img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

@media only screen and (max-width: 320px) {
  .hs-upc.hs-upc-compact {
    grid-template-columns: 1fr;
    grid-template-areas: "photo" "header" "details" "footer";
    max-width: 24em;
  }
}

@media only screen and (min-width: 540px) {
  .hs-upc {
    display: grid;
    grid-template-columns: 25% 1fr;
    grid-template-rows: min-content 1fr min-content;
    grid-template-areas: "photo header" "photo details" "footer footer";
  }
  .hs-upc .hs-upc-photo img {
    height: 100%;
    width: 100%;
    max-width: none;
    object-fit: cover;
  }
  .hs-upc .hs-upc-header {
    letter-spacing: 0.15rem;
  }
  .hs-upc .hs-upc-header .hs-upc-title {
    color: #5c6466;
    font-size: 1.25rem;
  }
  .hs-upc .hs-upc-v-group {
    justify-content: space-around;
  }
}

