:root {
  --hst-font-family: "Segoe UI", system-ui, "Apple Color Emoji", "Segoe UI Emoji", sans-serif;
  --hst-brand: #464775;
  --hst-text-primary: #242424;
  --hst-text-secondary: #424242;
  --hst-text-tertiary: #616161;
  --hst-background-primary: #f5f5f5;
  --hst-background-component: #ffffff;
  --hst-background-component-alt: #ebebeb;
  --hst-background-hover: #f5f5f5;
  --hst-grid-row-color: #242424;
  --hst-grid-header-color: #242424;
  --hst-background-header: #FFF;
  --hst-border-color: #f5f5f5;
  --hst-tab-default: #616161;
  --hst-tab-active: #242424;
  --hst-tab-border: var(--hst-border-color);
  --hst-tab-border-active: var(--hst-brand);
  --hst-tab-border-hover: #bdbdbd;
}

.hsbody.hsteams.v-dark .hs-textinput-refiner {
  background-color: var(--hst-background-component);
  color: var(--hst-text-primary);
  border-color: transparent;
}

.hsbody.hsteams.v-dark .hs-textinput-refiner ::placeholder {
  color: var(--light);
}

body.hsbody.hsteams.v-dark,
body.hsbody.hsteams.v-contrast {
  --hst-brand: #9EA2FF;
  --hst-background-primary: #1f1f1f;
  --hst-text-primary: #ffffff;
  --hst-text-secondary: #f5f5f5;
  --hst-text-tertiary: #bdbdbd;
  --hst-background-component: #3B3A39;
  --hst-background-component-alt: #424242;
  --hst-background-hover: #1f1f1f;
  --hst-grid-row-color: #D6D6D6;
  --hst-grid-header-color: #ffffff;
  --hst-background-header: #3B3A39;
  --hst-border-color: #1f1f1f;
  --hst-tab-active: #242424;
  --hst-tab-border: var(--hst-border-color);
  --hst-tab-border-active: var(--hst-brand);
}

.hsbody.hsteams {
  font-family: var(--hst-font-family);
  background-color: var(--hst-background-primary);
  color: var(--hst-text-primary);
}

.hsbody.hsteams .text-primary {
  color: var(--hst-text-primary) !important;
}

.hsbody.hsteams .text-secondary {
  color: var(--hst-text-secondary) !important;
}

.hsbody.hsteams .text-tertiary {
  color: var(--hst-text-tertiary) !important;
}

.hsbody.hsteams .k-card {
  background-color: var(--hst-background-component) !important;
  color: var(--hst-text-primary) !important;
}

.hsbody.hsteams .k-card .k-card-header {
  background-color: var(--hst-background-component) !important;
  color: var(--hst-text-primary) !important;
}

.hsbody.hsteams .k-card .k-card-header .k-card-subtitle {
  color: var(--hst-text-secondary) !important;
}

.hsbody.hsteams .k-card .k-listview,
.hsbody.hsteams .k-card .k-listview .k-listview-content {
  background-color: var(--hst-background-component);
}

.hsbody.hsteams .k-listview,
.hsbody.hsteams .k-listview .k-listview-content {
  background-color: var(--hst-background-primary);
  color: var(--hst-text-primary);
}

.hsbody.hsteams .list-group .list-group-item {
  background-color: var(--hst-background-component);
  color: var(--hst-text-primary);
  border-color: var(--hst-border-color);
}

.hsbody.hsteams .list-group .list-group-item:hover {
  background-color: var(--hst-background-hover);
}

.hsbody.hsteams table.table,
.hsbody.hsteams table.table td {
  background-color: var(--hst-background-component);
  color: var(--hst-text-primary);
  border-color: var(--hst-border-color);
}

.hsbody.hsteams .k-tabstrip-wrapper {
  min-height: 1.8rem;
  background-color: var(--hst-background-primary);
}

.hsbody.hsteams .k-tabstrip-wrapper .k-content.k-state-active {
  background-color: var(--hst-background-primary);
  border: none;
}

.hsbody.hsteams .k-tabstrip-wrapper .k-tabstrip ul.k-tabstrip-items {
  border: none;
  margin-bottom: 0;
  justify-content: inherit;
}

.hsbody.hsteams .k-tabstrip-wrapper .k-tabstrip ul.k-tabstrip-items li.k-item {
  font-weight: 400;
  font-size: 14px;
  height: 2.6rem;
  padding: 0;
  margin: 0 6px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  border: 0;
  border-radius: 0;
  max-width: 20rem;
  flex: inherit;
  background-color: var(--hst-background-primary) !important;
}

.hsbody.hsteams .k-tabstrip-wrapper .k-tabstrip ul.k-tabstrip-items li.k-item.k-first {
  margin-left: 0;
}

.hsbody.hsteams .k-tabstrip-wrapper .k-tabstrip ul.k-tabstrip-items li.k-item.k-last {
  margin-right: 0;
}

.hsbody.hsteams .k-tabstrip-wrapper .k-tabstrip ul.k-tabstrip-items li.k-item .k-link {
  color: var(--hst-text-primary) !important;
  padding-left: 0 !important;
  padding-right: 0 !important;
}

.hsbody.hsteams .k-tabstrip-wrapper .k-tabstrip ul.k-tabstrip-items li.k-item.k-state-active {
  box-shadow: none;
  background-color: var(--hst-background-primary);
  border-bottom: 3px solid var(--hst-tab-border-active) !important;
}

.hsbody.hsteams .k-tabstrip-wrapper .k-tabstrip ul.k-tabstrip-items li.k-item.k-state-active .k-link {
  color: var(--hst-text-secondary) !important;
  font-weight: 600;
}

.hsbody.hsteams .k-tabstrip-wrapper .k-tabstrip ul.k-tabstrip-items li.k-item.k-state-active:hover {
  border-bottom: 3px solid var(--hst-tab-border-active) !important;
}

.hsbody.hsteams .k-tabstrip-wrapper .k-tabstrip ul.k-tabstrip-items li.k-item:hover {
  border-bottom: 3px solid var(--hst-tab-border-hover) !important;
}

