//
// fork of bootstrap background classes, adds code to select an appropriate text color based on the background color 
// for easier maintenance the bootstrap mixins have also be forked here
//

@mixin bg-variant($parent, $color, $ignore-warning: false) {
  #{$parent} {
    background-color: $color !important;
    color: set-textcolor($color);
  }

  a#{$parent},
  button#{$parent} {
    @include hover-focus() {
      background-color: darken($color, 10%) !important;
      color: set-textcolor(darken($color, 10%));
    }
  }
}

@mixin bg-gradient-variant($parent, $color, $ignore-warning: false) {
  #{$parent} {
    background: $color linear-gradient(180deg, mix($body-bg, $color, 15%), $color) repeat-x !important;
    color: set-textcolor($color);
  }
}

@each $color,
$value in $theme-colors {
  @include bg-variant(".bg-#{$color}", $value, true);
}

@each $color,
$value in $hs-accent-colors {
  @include bg-variant(".hs-bg-#{$color}", $value, true);
}

@each $color,
$value in $hs-theme-grays {
  @include bg-variant(".hs-bg-gray-#{$color}", $value, true);
}

@if $enable-gradients {

  @each $color,
  $value in $theme-colors {
    @include bg-gradient-variant(".bg-gradient-#{$color}", $value, true);
  }
}

.bg-white {
  background-color: $white !important;
}

.bg-transparent {
  background-color: transparent !important;
}

// list-group-item-danger makes the font the darker red, this uses the error red as the background.
.hs-danger {
  background-color:$error;
  color:$white;
}