// if slim agenda is in a k-card, but not in a k-card-body, disable border 
.k-card div:not(.k-card-body) .hs-agenda-slim {
    border-width:0;
}

.hs-agenda-slim {

    .k-scheduler-header,
    .k-scheduler-navigation,
    .k-scheduler-refresh,
    .k-scheduler-timecolumn {
        display: none !important;
    }

    .k-scheduler-toolbar {
        background-color: transparent;
    }

    .k-scheduler-table td {
        white-space: normal;
    }

    td.k-scheduler-datecolumn {
        width: 90px;
        font-size: 12px;

        .hs-agenda-slim-datecolumn {
            display: flex;
            flex-direction: column;
            align-items: center;

            .hs-agenda-date {
                font-size: 1.75em;
            }

            .hs-agenda-day {
                font-size: 1.25em;
            }
        }
    }

}