// 
// Handshake Framework Variables 
// include hs variables, and override any bootstrap or kendo variables to override. 

//  
$fa-font-path: "./webfonts";

// SP2019 / Teams Default font. 
$font-family-base: "Segoe UI",system-ui,"Apple Color Emoji","Segoe UI Emoji",sans-serif;
/*
$font-family-base: Roboto,"Helvetica Neue",Arial,-apple-system,BlinkMacSystemFont,"Segoe UI","Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";*/ 
$hs-large-font-size:2.2em;
$hs-large-font-weight:300;
$hs-small-font-size:0.75em;
$hs-small-font-weight:400;

/* Aderant Color Pallette */
$accent: #263d4b;
$primary: $accent;
$secondary:#B30838;
$accent-contrast: #1c97bf;
$error: #7C0A02;
$warning: #FFCC00;
$success: #008e6f;
$info: #5c6466;
$light:#edf2f2;
$dark:#5c6466;
$white:#fff;

// additional accents for customer needs 
$accent3:#3FCFD5;
$accent4:#983222;
$accent5:#F0AB00;
$accent6:#8E9300;

$theme-colors: ("primary":$primary,
  "secondary":$secondary,
  "success":$success,
  "danger":$error,
  "info":$info,
  "warning":$warning,
  "light":$light,
  "dark":$dark,
  "white":$white
);

$hs-accent-colors: ("accent1":$accent,
  "accent2":$accent-contrast,
  "accent3":$accent3,
  "accent4":$accent4,
  "accent5":$accent5,
  "accent6":$accent6
);
//@debug map-get($hs-accent-colors, "accent3"); //output $accent3 value 

$hs-theme-grays: (
  "black":#000,
  "dark" : #50636D,
  "disabled" : #B4B4B4,
  "border" : #DCDCDC,
  "light"  : #F2F2F2,
  "white" : #FFF
);
//@each $type, $value in $hs-theme-grays {   @debug "gray #{$type}: #{$value}"; }

$k-theme-colors: ("primary":$primary,
  "info":$info,
  "success":$success,
  "warning":$warning,
  "error":$error,
);

// ms teams colors
// see https://fluentsite.z22.web.core.windows.net/0.51.7/color-schemes "brand"

$hsteam-brand:#464775;
$hsteam-brand-hover:#9EA2FF;

$hsteam-default-bg1: #f5f5f5; // Background Focus 1
$hsteam-default-bg2: #e2e2f6; // Background Focus 2
$hsteam-default-bg3: #373644; // Background Focus 3
$hsteam-default-bg-hover: #f4f4fc; // Background Hover 1
$hsteam-default-card-bg: #fff; // Background Focus Within
$hsteam-default-card-title-bg: #e2e2f6; // Background Active 1
$hsteam-default-border: #e2e2f6; // Border Active 1
$hsteam-default-text1: #323131;
$hsteam-default-text2: #292828;
$hsteam-default-tab: #616161;
$hsteam-default-tab-active: #242424;
$hsteam-default-tab-border-active:$hsteam-brand;
$hsteam-default-danger:#8D021F;

// ---- dark mode
$hsteam-dark-brand:#9EA2FF;
$hsteam-dark-bg1: #1f1f1f; // Background Focus 1
$hsteam-dark-bg2: #3B3A39; // Background Focus 2
$hsteam-dark-bg3: #373644; // Background Focus 3
$hsteam-dark-bg-hover: #a6a7dc; // Background Hover 1
$hsteam-dark-card-bg: #33344a; // Background 1
$hsteam-dark-border: #a6a7dc; // Border Active 1
$hsteam-dark-text1: #fff;
$hsteam-dark-text2: #f3f2f1;
$hsteam-dark-tab: #adadad;
$hsteam-dark-tab-active: #FFF;
$hsteam-dark-tab-border-active:#9EA2FF;
$hsteam-dark-danger:#7C0A02;

$hsteam-dark-grid-row:#292828;
$hsteam-dark-grid-row-hover:#333333;
$hsteam-dark-grid-row-color:#D6D6D6;

$hsteam-font-size-title:0.875rem;
$hsteam-font-size-subtitle:0.75rem;

// ---- teams tabs same for dark/default
$hsteam-tab-border:#bdbdbd;

// ^ ms teams colors

$base-text:#000;
$body-bg: #fff;
$body-color: $base-text;
$component-bg: $body-bg;
$component-color: #5c6466;

$border-radius: 0.25rem;

$link-color: $primary;

// Kendo Chart Variables 

$series-a: #00429d;
$series-b: #4771b2;
$series-c: #73a2c6;
$series-d: #a5d5d8;
$series-e: #ffffe0;
$series-f: #ffbcaf;
$series-g: #f4777f;
$series-h: #cf3759;
$series-i: #93003a;
$chart-major-lines: rgba(0, 0, 0, .08);
$chart-minor-lines: rgba(0, 0, 0, .04);


// TabColor is a Variable Specific to the Handshake Framework 
$tabcolor: ( // default
  "a" : $primary, "b" : $secondary, "c" : $success, "d" : $info, "e" : $warning,
  // samples....
  // blue palette 
  //"a": #25274d, "b": #464866, "c": #AAABB8, "d": #2e9cca, "e": #29648a,
  // purple pallete 
  // "a": #2e1114, "b": #501b1d, "c": #64485c, "d": #83677b, "e": #ADADAD, 
  // series pallette (see "series" above)
  //"a" : $series-a, "b" : $series-b, "c" : $series-c, "d" : $series-d, "e" : $series-e,
  // Youthful and Fun
  //"a" : #A64AC9, "b" : #FCCD04, "c" : #FFB48F, "d" : #F5e6cc, "e" : #17e9e0,

  "opacity" : .60,
  "shadow" : 0px 3px 6px 0px rgba(0, 0, 0, 0.5),
  "radius" : 18px,
);