// 
// Not to be confused with Kendo Grids or Bootstrap Grid Layout
// these create helper classes for using CSS Grids in Handshake Skin Layouts
//

$grid-gap: 1em;
$grid-mincol: map-get($container-max-widths, "sm") / 2;

@mixin hs-grid-common() {
  display: grid;
  grid-gap: $grid-gap;

  grid-template-columns: minmax($grid-mincol, 1fr);

  // define all grid areas, even though some implementations don't use them all.
  &>.sidebar {
    grid-area: sidebar;
  }

  &>.sidebar1 {
    grid-area: sidebar1;
  }

  &>.sidebar2 {
    grid-area: sidebar2;
  }

  &>.main {
    grid-area: main;
  }

  &>.footer {
    grid-area: footer;
  }

  &>.header {
    grid-area: header;
  }
}

.hs-grid-nogap {
  grid-gap: 0 !important;
}

.hs-grid-gap-lg {
  grid-gap: 2.5em !important;
}

.hs-grid-gap-md {
  grid-gap: 1.25em !important;
}

.hs-grid-autofill {
  display:grid;
  grid-gap:$grid-gap;
  grid-template-columns: repeat(auto-fill, minmax(320px,1fr));
}

.hs-grid-autofit {
  display:grid;
  grid-gap:$grid-gap;
  grid-template-columns: repeat(auto-fit, minmax(320px,1fr));
}

.hs-grid {
  @include hs-grid-common();

  @media screen and (min-width: map-get($container-max-widths, "md")) {
    grid-gap: $grid-gap;
    grid-template-columns: repeat(auto-fill, minmax($grid-mincol, 1fr));

    .hs-grid-double {
      grid-column: span 2;
    }

    .hs-grid-triple {
      grid-column: span 3;
    }

    .hs-grid-quad {
      grid-column: span 4;
    }
  }
}

.hs-grid-84split,
.hs-grid-48split {
  @include hs-grid-common();
  grid-template-areas: "sidebar1""main";

  @media screen and (min-width: map-get($container-max-widths, "md")) {
    grid-gap: $grid-gap;
    grid-template-columns: repeat(3, minmax($grid-mincol, 1fr));
    grid-template-areas: "sidebar1 main main";
  }
}

.hs-grid-84split {
  grid-template-areas: "main""sidebar1";

  @media screen and (min-width: map-get($container-max-widths, "md")) {
    grid-template-areas: "main main sidebar1";
  }
}

.hs-grid-magazine {
  @include hs-grid-common();
  // stacked for mobile
  grid-template-areas: "header""main""sidebar1""sidebar2""footer";

  // tablet screen
  @include media-breakpoint-between("md", "lg", $grid-breakpoints) {
    grid-gap: $grid-gap;
    grid-template-columns: minmax($grid-mincol, 1fr) minmax($grid-mincol, 2fr);
    grid-template-rows: auto;
    grid-template-areas:
      "header header"
      "sidebar1 main"
      "sidebar2 main"
      "footer footer";

    &.hs-grid-content-left {
      grid-template-columns: minmax($grid-mincol, 2fr) minmax($grid-mincol, 1fr);
      grid-template-areas:
        "header header"
        "main sidebar1"
        "main sidebar2"
        "footer footer";
    }

    &.hs-grid-topbars {
      grid-template-columns: repeat(2, minmax($grid-mincol, 1fr));
      grid-template-areas:
        "header header"
        "sidebar1 sidebar2"
        "main main"
        "footer footer";
    }
  }

  // x-large monitors
  @media screen and (min-width: map-get($container-max-widths, "lg")+1) {
    grid-gap: $grid-gap;
    grid-template-columns: minmax($grid-mincol, 1fr) minmax($grid-mincol, 2fr) minmax($grid-mincol, 1fr);
    grid-template-rows: auto;
    grid-template-areas:
      "header header header"
      "sidebar1 main sidebar2"
      "footer footer footer";
  }
}

.hs-grid-resp2col {
  @include hs-grid-common();
  //grid-template-columns: repeat(5, 1fr);
  grid-template-columns: auto auto 1fr auto auto; // accommodate a fixed width for the sidebar 

  grid-template-areas: "header header header header header"
    "sidebar sidebar sidebar sidebar sidebar"
    "main main main main main"
    "footer footer footer footer footer";

  .sidebar {
    max-width: 100%; // make the sidebar 100% on mobile first...
  }

  @include media-breakpoint-between("md", "xl", $grid-breakpoints) {
    grid-gap: $grid-gap;
    grid-template-areas:
      "header header header header header"
      "main main main sidebar sidebar"
      "footer footer footer footer footer";

    &.hs-rtl {
      grid-template-areas:
        "header header header header header"
        "sidebar sidebar main main main"
        "footer footer footer footer footer";
    }

    @include media-breakpoint-up("xl",  $grid-breakpoints) {
      grid-gap: $grid-gap;
      grid-template-areas:
        "header header header header header"
        "main main main main sidebar"
        "footer footer footer footer footer";
    }    

    &.hs-rtl {
      grid-template-areas:
        "header header header header header"
        "sidebar main main main main"
        "footer footer footer footer footer";
    }
  }

}


.hs-grid-demoware {
  /* DemoWare Specific Styles: add demoware class to the container to see the layout without real content */
  margin: 0 auto;
  padding: 0;

  .header,
  .main,
  .sidebar,
  .sidebar1,
  .sidebar2,
  .footer {
    background: #eaeaea;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50vw;
  }

  .header {
    height: 10vw;

    &:after {
      content: "Header Goes Here";
    }
  }

  .footer {
    height: 10vw;

    &:after {
      content: "Footer Goes Here";
    }
  }

  .main:after {
    content: "Main Body Goes Here";
  }

  .sidebar,
  .sidebar1,
  .sidebar2 {
    &:after {
      content: "Sidebar Goes Here";
    }
  }
}

// simple card layout with image on left
.hs-card-img-left {
  background-color:$component-bg;
  color:$component-color;
  border: $listview-border-width;
  border-color: $listview-border;
  border-style: solid;
  border-radius: $border-radius;
  padding: $cell-padding-y $cell-padding-x;
  display: grid;
  grid-template-columns: 150px 1fr;
  grid-gap: 1em;
  align-items:center;
  &:hover {
    background-color:darken($component-bg, 10%);
    img.hs-card-img-left-image {
      border-radius: 0;  
    }
  }

  img.hs-card-img-left-image {
    width: 150px;
    border-radius: 50%;
    &.square {
      border-radius: 0;
    }
  }

  .hs-card-img-left-body {

    .hs-card-img-left-title {
      font-size: $hs-large-font-size;
      font-weight: $hs-large-font-weight;
    }

    .hs-card-img-left-subtitle {
      font-size: $hs-small-font-size;
      font-weight: $hs-small-font-weight;
    }
  }
}